.feedback__impression-item,
.feedback__landmarks-item {
  position: relative;
}
.feedback__impression-input,
.feedback__landmarks-input {
  appearance: none;
  margin-left: 0;
  box-sizing: border-box;
}
.feedback__impression-input + label::before {
  content: "";
  position: absolute;
  box-sizing: border-box;
  bottom: -15px;
  display: block;
  width: 56px;
  height: 56px;
  background-image: url("../img/checkbox-off.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.feedback__impression-input:hover + label::before {
  background-image: url("../img/checkbox-off-hover.svg");
}
.feedback__impression-input:active + label::before {
  background-image: url("../img/checkbox-off-active.svg");
}
.feedback__impression-input:checked + label::before {
  background-image: url("../img/checkbox-on.svg");
}
.feedback__impression-input:checked:hover + label::before {
  background-image: url("../img/checkbox-on-hover.svg");
}
.feedback__impression-input:checked:active + label::before {
  background-image: url("../img/checkbox-on-active.svg");
}
.feedback__impression-input:focus + label::before,
.feedback__impression-input:checked:focus + label::before {
  border: 2px solid #a3c4ff;
  border-radius: 50%;
  background-position: -1px -1px;
}
.feedback__landmarks-input + label::before {
  content: "";
  position: absolute;
  box-sizing: border-box;
  bottom: -15px;
  display: block;
  width: 56px;
  height: 56px;
  background-color: #f2f2f2;
  border-radius: 5px;
}
.feedback__landmarks-input:hover + label::before {
  background-color: #e5e5e5;
}
.feedback__landmarks-input:active + label::before {
  background-color: #d5d5d5;
}
.feedback__landmarks-input:checked + label::before {
  content: "";
  position: absolute;
  box-sizing: border-box;
  bottom: -15px;
  display: block;
  width: 56px;
  height: 56px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #f2f2f2;
  border-radius: 5px;
}
.feedback__landmarks-input:checked:hover + label::before {
  background-color: #e5e5e5;
}
.feedback__landmarks-input:checked:active + label::before {
  background-image: url("../img/icon-tick-active.svg");
  background-color: #d5d5d5;
}
.feedback__landmarks-input:focus + label::before,
.feedback__landmarks-input:checked:focus + label::before {
  border: 2px solid #a3c4ff;
  border-radius: 5px;
}
.feedback__contacts-input--phone {
